import * as React from 'react';
import { useField } from 'formik';
// @ts-ignore
import style from './FormikForms.module.css'
// @ts-ignore
import errorIcon from './../../assets/images/error.png'



export const MyTextInput = ({ label, ...props }) => {
    // useField() returns [formik.getFieldProps(), formik.getFieldMeta()]
    // which we can spread on <input>. We can use field meta to show an error
    // message if the field is invalid and it has been touched (i.e. visited)
    // @ts-ignore
    const [field, meta] = useField(props);
    return (
        <>
            <label htmlFor={props.id || props.name}>{label}</label>
            <input className={meta.touched && meta.error ? style.errorForm: style.input} {...field} {...props} />
            {meta.touched && meta.error ? (
                <div className={style.error}>
                    <img src={errorIcon} alt=""/>
                    {meta.error}</div>
            ) : null}
        </>
    );
};

export const MyTextArea = ({ label, ...props }) => {

    // @ts-ignore
    const [field, meta] = useField(props);
    return (
        <>
            <textarea className={meta.touched && meta.error ? style.errorTextForm: style.textArea} {...field} {...props} />
            {meta.touched && meta.error ? (
                <div className={style.error}>
                    <img src={errorIcon} alt=""/>
                    {meta.error}</div>
            ) : null}
        </>
    );
};

export const MyCheckbox = ({ children, ...props }) => {
    // React treats radios and checkbox inputs differently other input types, select, and textarea.
    // Formik does this too! When you specify `type` to useField(), it will
    // return the correct bag of props for you -- a `checked` prop will be included
    // in `field` alongside `name`, `value`, `onChange`, and `onBlur`
    // @ts-ignore
    const [field, meta] = useField({ ...props, type: 'checkbox' });
    return (
        <div>
            <label className="checkbox-input">
                <input type="checkbox" {...field} {...props} />
                {children}
            </label>
            {meta.touched && meta.error ? (
                <div className={style.error}>
                    <img src={errorIcon} alt=""/>
                    {meta.error}</div>
            ) : null}
        </div>
    );
};

export const MySelect = ({ label, ...props }) => {
    // @ts-ignore
    const [field, meta] = useField(props);
    return (
        <div>
            <label htmlFor={props.id || props.name}>{label}</label>
            <select {...field} {...props} />
            {meta.touched && meta.error ? (
                <div className={style.error}>
                    <img src={errorIcon} alt=""/>
                    {meta.error}</div>
            ) : null}
        </div>
    );
};



